import React from "react";
import s from "../Projects.module.scss";
import {ProjectDataItem} from "../../../utils/data";

type ProjectItemProps = {
    project: ProjectDataItem;
    style: { backgroundImage: string }
}

export const ProjectItem: React.FC<ProjectItemProps> = ({project, style}) => {

    const techList = project.techList.map((tech, index) => <li key={index}>{tech}</li>)

    return (
        <div className={s.feature}>
            <div className={s.projectContent}>
                <div>
                    <p className={s.projectOverline}>Featured Project</p>
                    <h3 className={s.projectTitle}>{project.projectName}</h3>
                    <div className={s.projectDescription}>
                        {project.description}
                    </div>
                    <ul className={s.projectTechList}>
                        {techList}
                    </ul>
                    <div className={s.projectLinks}>

                        <a href={project.socialLinks[1]} aria-label="External Link" className={s.external}
                           target="_blank">
                            <svg xmlns="http://www.w3.org/2000/svg" role="img" viewBox="0 0 24 24" fill="none"
                                 stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                            <title>External Link</title>
                                <path d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"/>
                                <polyline points="15 3 21 3 21 9"/>
                                <line x1="10" y1="14" x2="21" y2="3"/>
                            </svg>
                        </a>

                        <a href={project.socialLinks[0]} aria-label="GitHub Link"
                           target="_blank">
                            <svg xmlns="http://www.w3.org/2000/svg" role="img" viewBox="0 0 24 24" fill="none"
                                 stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                <title>GitHub</title>
                                <path
                                    d="M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54
                                     6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38
                                     13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5
                                      3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22"/>
                            </svg>
                        </a>
                    </div>
                </div>
            </div>
            <div className={s.projectImage} style={style}>

            </div>
        </div>
    );
};

